___CSS_LOADER_EXPORT___.push([module.id, `body, html {
  margin: 0;
  padding: 0;
  font-family: system-ui, sans-serif;
  background: #181818;
  color: #f3f3f3;
  height: 100vh;
}
.container {
  display: flex;
  flex-direction: column;
  height: 100vh;
}
.chat-history {
  flex: 1;
  overflow-y: auto;
  padding: 16px;
  background: #23272e;
}
.user-msg {
  text-align: right;
  margin-bottom: 12px;
}
.ai-msg {
  text-align: left;
  margin-bottom: 12px;
}
.input-bar {
  display: flex;
  padding: 12px;
  background: #20232a;
  border-top: 1px solid #333;
}
.input-bar input {
  flex: 1;
  padding: 8px;
  border-radius: 4px;
  border: none;
  margin-right: 8px;
  font-size: 1em;
}
.input-bar button {
  background: #0078d4;
  color: #fff;
  border: none;
  border-radius: 4px;
  padding: 8px 16px;
  cursor: pointer;
  font-size: 1em;
}
.attachments {
  margin-top: 4px;
  font-size: 0.9em;
  color: #b3b3b3;
}
.attachment {
  display: inline-block;
  margin-right: 8px;
}
